<template>

	<view>
		<wTitleBar 
		title="活动详情" 
		:isReturn='true' 
		bgColor="#213244" 
		textAlign='centers'
		textColor="#fff"></wTitleBar>
		<view>
			{{hd.title}}
		</view>
		<view >
			<image :src="hd.logo" :lazy-load='true' class="logo">
			
		</view>
		<view class="neirong">
			{{hd.content}}
		</view>
		<view class="shijian">
			活动结束时间：{{hd.endTime}}
		</view>
	</view>
</template>

<script>
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		data() {
			return {
				hd:[],
			}
		},
		onLoad(option) {
			this.init(option.id);
		},
		methods: {
			init(hid){
				this.AkxhHdlists(hid);
			},
			AkxhHdlists(hid){
				AkxhHd('/catalyzer/AkxhHd/query-by-id','GET',{'id':hid}).then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						this.hd = result;
						this.hd.logo = this.imgURL + result.logo;
						this.hd.endTime = result.endTime.substring(0,10);
					}
				})
			}
		}
	}
</script>

<style>
	.content {
	  background-color: #f8f8f8;
	}
	.content .title{
		text-align: center;
	}
	.content .logo{
		width: 100%;
		height: 200px;
	}
	.content .neirong{
		height:auto; 
		min-height:0px;
	}
	.content .shijian{
		font-size: 20px;
		color: #a7a7a7;
	}
</style>